*{
    margin:0px;
    padding:0px;
    box-sizing: border-box;
}
body{
    background:url(counter-background.jpg);
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    overflow: hidden;
}
.App{
    padding:20px;
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    background:transparent;
    box-shadow: 0 25px 50px rgba(0,0,0,0.5);
    backdrop-filter: blur(10px);
}
.counterNumber{
    text-align:center;
    font-size:40px;
}
.manual-controls{
    display:flex;
    justify-content: space-around;
}
button{
    padding:5px;
    font-size:20px;
    margin:5px 0px;
}
input{
    margin:10px 0px;
    padding:5px;
    font-size:20px;
    text-align:center;
    border-radius:20px;
    border:none;
    outline: none;
}
.input-controls{
    display:flex;
    flex-direction: column;
    align-items: center;
}
.input-controls button{
    padding:5px;
    font-size:20px;
    width:100px;
}
.timer-controls{
    display:flex;
    justify-content: space-around;
}